import React from "react"
import { Link, graphql } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
import { rhythm } from "../utils/typography"

import {talks, writings} from "../../content/more.js"

class BlogIndex extends React.Component {
  render() {
    const { data } = this.props
    const siteTitle = data.site.siteMetadata.title
    const blog = data.allMarkdownRemark.edges[0]
    const blogTitle = blog.node.frontmatter.title || blog.node.fields.slug

    return (
      <Layout location={this.props.location} title={siteTitle}>
        <SEO title="Home" />
        <h1 style={{textAlign: 'center', marginBottom: '0'}}>
          Loris Cro
        </h1>
        <small style={{textAlign: 'center', display: 'block'}}>
          <i>Personal Website</i>
        </small>

        <div style={{textAlign: 'center'}}>
          <h4 style={{marginTop: rhythm(1), marginBottom: rhythm(1/4), color: 'darkgrey', fontWeight: 100}}> 
            <small>Latest Blog Post</small>
          </h4>
          <h3
            style={{
              margin: 0,
              marginBottom: rhythm(1 / 5),
            }}
          >
            <Link style={{ boxShadow: `none` }} to={blog.node.fields.slug}>
              {blogTitle}
            </Link>
          </h3>
          <small>{blog.node.frontmatter.date}</small>
        </div>

        <div style={{textAlign: 'center'}}>
          <h4 style={{marginTop: rhythm(1), marginBottom: rhythm(1/4), color: 'darkgrey', fontWeight: 100}}> 
            <small>Latest Talk</small>
          </h4>
          <h3
            style={{
              margin: 0,
              marginBottom: rhythm(1 / 5),
            }}
          >
            <a style={{ boxShadow: `none` }} href={talks[0].url}>
              {talks[0].title}
            </a>
          </h3>
          <small>{talks[0].date} • {talks[0].conf}</small>
        </div>

        <div style={{textAlign: 'center'}}>
          <h4 style={{marginTop: rhythm(1), marginBottom: rhythm(1/4), color: 'darkgrey', fontWeight: 100}}> 
            <small>Latest Article</small>
          </h4>
          <h3
            style={{
              margin: 0,
              marginBottom: rhythm(1 / 5),
            }}
          >
            <a style={{ boxShadow: `none` }} href={writings[0].url}>
              {writings[0].title}
            </a>
          </h3>
          <small>{writings[0].date} • {writings[0].committent}</small>
        </div>

      </Layout>
    )
  }
}

export default BlogIndex

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      filter: {frontmatter: {draft: {eq: false}}}
      sort: { fields: [frontmatter___date], order: DESC }, limit: 1) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            description
          }
        }
      }
    }
  }
`
